import React from "react";
import "bootstrap/dist/css/bootstrap.min.css";
import { Button, Form, Card, Jumbotron, Row, Col } from "react-bootstrap";
function App() {
  const [search, setSearch] = React.useState("");
  const [newsdata, setNewsData] = React.useState([]);

  const getData = async () => {
    fetch(
      `https://gnews.io/api/v4/search?q=${search}&token=7fbc9d4ebd469e05a634df707bce119d`
    )
      .then((response) => response.json())
      .then((data) => setNewsData(data.articles));
  };
  const handleOnChange = (e) => {
    setSearch(e.target.value);
  };
  return (
    <React.Fragment>
      <Jumbotron>
        <Form
          inline
          style={{
            marginLeft: "20px",
            marginTop: "20px",
          }}
        >
          <Row>
            <Col>
              <Form.Control
                placeholder="News at your finger tips........Search!"
                onChange={handleOnChange}
                style={{ width: "30rem", marginLeft: "22rem" }}
              />
            </Col>
            <Col>
              <Button variant="dark" onClick={getData}>
                Submit
              </Button>
            </Col>
          </Row>
        </Form>
      </Jumbotron>

      {newsdata &&
        newsdata.map((items) => (
          <div
            className="card mb-3"
            style={{
              maxWidth: "58rem",
              minWidth: "58rem",
              border: "2px solid #e9ecef",
              borderRadius: "15px",
              justifyContent: "center",
              margin: "0 auto",
            }}
          >
            <div className="row g-0">
              <div className="col-md-4">
                <img
                  src={items.image}
                  style={{
                    maxWidth: "20rem",
                    alignContent: "center",
                    borderRadius: "10px",
                  }}
                />
              </div>
              <div className="col-md-8">
                <div className="card-body">
                  <h5 className="card-title">{items.title}</h5>
                  <p className="card-text">{items.description}</p>
                </div>
              </div>
            </div>
          </div>
        ))}
    </React.Fragment>
  );
}

export default App;
